import React, {
  useState,
  useEffect,
  FormEvent,
  useCallback,
  useRef
} from 'react';
import { useRouter } from 'next/router';
import TiptapEditor from '../../component/TiptapEditor';
import {
  writeFeed,
  readPost,
  updatePost,
  uploadImage,
  getPostTags
} from '../../api/blogService/blog';
import { PostCreationRequestDTO, PostEntity } from '../../types/blogService/blogType';
import styles from '../../styles/blogService/write.module.css';

// Props íƒ€ì… ì •ì˜
interface WritePageProps {
  postId?: number; // ìˆ˜ì • ëª¨ë“œì¼ ë•Œë§Œ ì¡´ì¬
}

const WritePage: React.FC<WritePageProps> = ({ postId }) => {
  const router = useRouter();
  const [isComposing, setIsComposing] = useState(false);
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [tags, setTags] = useState<string[]>([]);
  const [tagInput, setTagInput] = useState('');
  const [thumbnailUrl, setThumbnailUrl] = useState<string | null>(null);
  const [isThumbnailUploading, setIsThumbnailUploading] = useState(false);
  const thumbnailInputRef = useRef<HTMLInputElement>(null);

  const [isPublished, setIsPublished] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [isInitialLoad, setIsInitialLoad] = useState(false);
  const [userSignId, setUserSignId] = useState<string | null>(null);

  // 1. [ì‚¬ìš©ì ID ë¡œë“œ]
  useEffect(() => {
    const id = localStorage.getItem('userSignId');
    if (id) {
      setUserSignId(id);
    }
  }, [router]);


  // 2. [ìˆ˜ì • ëª¨ë“œ] ë°ì´í„° ë¡œë“œ ë¡œì§
  useEffect(() => {
    if (postId && !isInitialLoad) {
      const loadPostData = async () => {
        try {
          setIsLoading(true);
          const data = await readPost(postId);

          setTitle(data.title);
          setContent(data.content);
          const loadedTags = await getPostTags(postId);
          setTags(loadedTags);
          console.log(loadedTags)
          setThumbnailUrl(data.thumbnail || null);
          setIsPublished(data.isPublished);
        } catch (error) {
          console.error("ë°ì´í„° ë¡œë“œ ì‹¤íŒ¨:", error);
          // âš ï¸ alert ëŒ€ì‹  ì‚¬ìš©ì ì •ì˜ ëª¨ë‹¬ ë˜ëŠ” ì¸ë¼ì¸ ë©”ì‹œì§€ ì‚¬ìš© ê¶Œì¥
          alert("ê²Œì‹œê¸€ ì •ë³´ë¥¼ ë¶ˆëŸ¬ì˜¤ëŠ”ë° ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤.");
          router.back();
        } finally {
          setIsLoading(false);
          setIsInitialLoad(true);
        }
      };
      loadPostData();
    }
  }, [postId, isInitialLoad, router]);


  // 3. íƒœê·¸ ê´€ë¦¬ ë¡œì§
  const handleTagKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    // âš ï¸ [í•µì‹¬ ìˆ˜ì •] í•œê¸€ ì¡°í•©(Composition) ì¤‘ì´ë¼ë©´ Enter, Spacebar ì²˜ë¦¬ ìŠ¤í‚µ
    // 'ê°€ë‚˜ë‹¤ë¼' ì…ë ¥ í›„ ' ' (ê³µë°±) ì…ë ¥ ì‹œ 'ë¼'ê°€ ë¶„ë¦¬ë˜ëŠ” ë²„ê·¸ ë°©ì§€
    if (isComposing) {
      return;
    }

    // Enter ë˜ëŠ” Spacebarë¥¼ ëˆŒë €ì„ ë•Œ
    if (e.key === 'Enter' || e.key === ' ') {
      e.preventDefault();
      const newTag = tagInput.trim();

      // íƒœê·¸ê°€ ì‹¤ì œë¡œ ì…ë ¥ë˜ì—ˆê³ , ê¸°ì¡´ íƒœê·¸ ëª©ë¡ì— í¬í•¨ë˜ì–´ ìˆì§€ ì•Šì„ ë•Œë§Œ ì¶”ê°€
      if (newTag.length > 0 && !tags.includes(newTag)) {
        setTags([...tags, newTag]);
      }
      setTagInput(''); // ì…ë ¥ í•„ë“œ ì´ˆê¸°í™”
    }
  };

  const removeTag = (tagToRemove: string) => {
    setTags(tags.filter(tag => tag !== tagToRemove));
  };


  // ì¸ë„¤ì¼ íŒŒì¼ ì—…ë¡œë“œ í•¸ë“¤ëŸ¬
  const handleThumbnailUpload = useCallback(async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    setIsThumbnailUploading(true);
    try {
      const result = await uploadImage(file);
      const url = (result as any).url;
      setThumbnailUrl(url);
      // âš ï¸ alert ëŒ€ì‹  ì‚¬ìš©ì ì •ì˜ ëª¨ë‹¬ ë˜ëŠ” ì¸ë¼ì¸ ë©”ì‹œì§€ ì‚¬ìš© ê¶Œì¥
      alert('ì¸ë„¤ì¼ ì—…ë¡œë“œ ì™„ë£Œ');
    } catch (error) {
      console.error('ì¸ë„¤ì¼ ì—…ë¡œë“œ ì‹¤íŒ¨:', error);
      // âš ï¸ alert ëŒ€ì‹  ì‚¬ìš©ì ì •ì˜ ëª¨ë‹¬ ë˜ëŠ” ì¸ë¼ì¸ ë©”ì‹œì§€ ì‚¬ìš© ê¶Œì¥
      alert('ì¸ë„¤ì¼ ì—…ë¡œë“œì— ì‹¤íŒ¨í–ˆìŠµë‹ˆë‹¤. (ì½˜ì†” í™•ì¸)');
      setThumbnailUrl(null);
    } finally {
      setIsThumbnailUploading(false);
      if (thumbnailInputRef.current) {
        thumbnailInputRef.current.value = '';
      }
    }
  }, []);


  // 4. [ì €ì¥/ìˆ˜ì •] í•¸ë“¤ëŸ¬ ë¡œì§
  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    if (!title.trim() || !content.trim()) {
      // âš ï¸ alert ëŒ€ì‹  ì‚¬ìš©ì ì •ì˜ ëª¨ë‹¬ ë˜ëŠ” ì¸ë¼ì¸ ë©”ì‹œì§€ ì‚¬ìš© ê¶Œì¥
      alert("ì œëª©ê³¼ ë‚´ìš©ì„ ì…ë ¥í•´ì£¼ì„¸ìš”.");
      return;
    }

    if (!userSignId) {
      // âš ï¸ alert ëŒ€ì‹  ì‚¬ìš©ì ì •ì˜ ëª¨ë‹¬ ë˜ëŠ” ì¸ë¼ì¸ ë©”ì‹œì§€ ì‚¬ìš© ê¶Œì¥
      alert("ë¡œê·¸ì¸ ì •ë³´ê°€ ì—†ìŠµë‹ˆë‹¤. ë‹¤ì‹œ ë¡œê·¸ì¸í•´ì£¼ì„¸ìš”.");
      router.push('/login');
      return;
    }

    setIsLoading(true);

    try {
      const postData: PostCreationRequestDTO = {
        title,
        content,
        tags,
        isPublished,
        thumbnail: thumbnailUrl || undefined,
      };

      let resultPost: PostEntity;

      if (postId) {
        resultPost = await updatePost(postId, postData, userSignId);
        // âš ï¸ alert ëŒ€ì‹  ì‚¬ìš©ì ì •ì˜ ëª¨ë‹¬ ë˜ëŠ” ì¸ë¼ì¸ ë©”ì‹œì§€ ì‚¬ìš© ê¶Œì¥
        alert("ê²Œì‹œê¸€ì´ ì„±ê³µì ìœ¼ë¡œ ìˆ˜ì •ë˜ì—ˆìŠµë‹ˆë‹¤.");
        router.push(`/post/${postId}`);

      } else {
        resultPost = await writeFeed(postData, userSignId);

        if (resultPost.postId) {
          // âš ï¸ alert ëŒ€ì‹  ì‚¬ìš©ì ì •ì˜ ëª¨ë‹¬ ë˜ëŠ” ì¸ë¼ì¸ ë©”ì‹œì§€ ì‚¬ìš© ê¶Œì¥
          alert("ê¸€ì´ ì„±ê³µì ìœ¼ë¡œ ë“±ë¡ë˜ì—ˆìŠµë‹ˆë‹¤!");
          router.push(`/post/${resultPost.postId}`);
        } else {
          // âš ï¸ alert ëŒ€ì‹  ì‚¬ìš©ì ì •ì˜ ëª¨ë‹¬ ë˜ëŠ” ì¸ë¼ì¸ ë©”ì‹œì§€ ì‚¬ìš© ê¶Œì¥
          alert("ê¸€ì´ ë“±ë¡ë˜ì—ˆìœ¼ë‚˜ IDë¥¼ ë°›ì§€ ëª»í–ˆìŠµë‹ˆë‹¤. í™ˆìœ¼ë¡œ ì´ë™í•©ë‹ˆë‹¤.");
          router.push('/');
        }
      }

    } catch (error) {
      console.error("ì‘ì—… ì¤‘ ì˜¤ë¥˜ ë°œìƒ:", error);
      // âš ï¸ alert ëŒ€ì‹  ì‚¬ìš©ì ì •ì˜ ëª¨ë‹¬ ë˜ëŠ” ì¸ë¼ì¸ ë©”ì‹œì§€ ì‚¬ìš© ê¶Œì¥
      alert("ì‘ì—… ì¤‘ ì˜¤ë¥˜ê°€ ë°œìƒí–ˆìŠµë‹ˆë‹¤. ìì„¸í•œ ë‚´ìš©ì€ ì½˜ì†”ì„ í™•ì¸í•´ì£¼ì„¸ìš”.");
    } finally {
      setIsLoading(false);
    }
  };


  if (isLoading && !isInitialLoad) {
    return (
        <div className={styles.writePageContainer}>
          <p>ë°ì´í„°ë¥¼ ë¶ˆëŸ¬ì˜¤ëŠ” ì¤‘ì…ë‹ˆë‹¤...</p>
        </div>
    );
  }

  return (
      <div className={styles.writePageContainer}>


        <form onSubmit={handleSubmit} className={styles.form}>

          {/* ======================================================= */}
          {/* ì œëª©ê³¼ ì¸ë„¤ì¼ì„ ê°ì‹¸ëŠ” ê·¸ë£¹ ì»¨í…Œì´ë„ˆ (1ì—´/ì„¸ë¡œ) */}
          {/* ======================================================= */}
          <div className={styles.titleThumbnailGroup}>

            {/* 1. ì œëª© ì…ë ¥ (titleInputWrapper) */}
            <div className={styles.titleInputWrapper}>
              <div className={styles.writeHeader}>
                <h1 className={styles.writeTitle}>{postId ? "ê²Œì‹œê¸€ ìˆ˜ì •" : "ìˆœê°„ê³¼ ìˆœê°„ì´ ëª¨ì—¬ ì‚¶ì„ ì´ë£¨ë©°"}</h1>
              </div>
              <label className={styles.label}>ì œëª©</label>

              <input
                  type="text"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                  placeholder="ì˜¤ëŠ˜ì˜ ì´ì•¼ê¸°ë¥¼ ë“¤ë ¤ì£¼ì„¸ìš”"
                  className={styles.titleInput}
                  required
              />

            </div>

            {/* 2. ì¸ë„¤ì¼ ì„¹ì…˜ */}
            <div className={styles.thumbnailSection}>
              <h3>ëŒ€í‘œ ì¸ë„¤ì¼ ì„¤ì •</h3>
              <div
                  className={styles.thumbnailWrapper}
                  onClick={() => !isThumbnailUploading && thumbnailInputRef.current?.click()}
                  // ë™ì  ì»¤ì„œ ìŠ¤íƒ€ì¼ë§Œ ì¸ë¼ì¸ìœ¼ë¡œ ìœ ì§€.
                  style={{
                    cursor: isThumbnailUploading ? 'not-allowed' : 'pointer',
                  }}
              >
                {thumbnailUrl ? (
                    // ì¸ë„¤ì¼ ë¯¸ë¦¬ë³´ê¸°
                    <img
                        src={thumbnailUrl}
                        alt="Thumbnail Preview"
                        className={styles.thumbnailPreview}
                    />
                ) : (
                    // ì—…ë¡œë“œ ë²„íŠ¼/ì•ˆë‚´ í…ìŠ¤íŠ¸
                    <div style={{ textAlign: 'center', color: '#666' }}>
                      {isThumbnailUploading ? 'ì—…ë¡œë“œ ì¤‘...' : 'í´ë¦­í•˜ì—¬ ì¸ë„¤ì¼ ì´ë¯¸ì§€ ì—…ë¡œë“œ (ì„ íƒ ì‚¬í•­)'}
                    </div>
                )}
                {/* ì‹¤ì œ íŒŒì¼ ì…ë ¥ (ìˆ¨ê¹€) */}
                <input
                    type="file"
                    ref={thumbnailInputRef}
                    accept="image/*"
                    onChange={handleThumbnailUpload}
                    style={{ display: 'none' }}
                    disabled={isThumbnailUploading}
                />
              </div>

              {/* ì¸ë„¤ì¼ ì œê±° ë²„íŠ¼ */}
              {thumbnailUrl && (
                  <button
                      type="button"
                      onClick={() => setThumbnailUrl(null)}
                      disabled={isThumbnailUploading}
                      className={styles.removeThumbnailBtn}
                  >
                    ì¸ë„¤ì¼ ì œê±°
                  </button>
              )}
            </div>
            {/* ì¸ë„¤ì¼ ì„¹ì…˜ ë */}
          </div>
          {/* titleThumbnailGroup ë */}
          {/* ======================================================= */}


          <div>
            <label className={styles.label}>ë‚´ìš©</label>
            <TiptapEditor
                content={content}
                onChange={setContent}
            />
          </div>

          <div className={styles.tagInputGroup}>
            <label className={styles.label}>íƒœê·¸</label>

            {/* íƒœê·¸ ëª©ë¡ í‘œì‹œ */}
            <div className={styles.tagList}>
              {tags.map(tag => (
                  <span
                      key={tag}
                      className={styles.tagItem}
                  >
                #{tag}
                    <button
                        type="button"
                        onClick={() => removeTag(tag)}
                        className={styles.removeTagBtn}
                    >
                    Ã—
                </button>
              </span>
              ))}
            </div>

            {/* íƒœê·¸ ì…ë ¥ í•„ë“œ */}
            <input
                type="text"
                value={tagInput}
                onChange={(e) => setTagInput(e.target.value)}
                // âœ… í•œê¸€ ì¡°í•© ì‹œì‘/ì¢…ë£Œ ì´ë²¤íŠ¸ ì¶”ê°€
                onCompositionStart={() => setIsComposing(true)}
                onCompositionEnd={() => setIsComposing(false)}
                onKeyDown={handleTagKeyDown}
                placeholder="íƒœê·¸ë¥¼ ì…ë ¥í•˜ê³  Enter"
                className={styles.textInput}
            />
            <small className={styles.hint}>íƒœê·¸ë¥¼ ì…ë ¥í•˜ê³  ì—”í„°ë¥¼ ëˆ„ë¥´ë©´ ì¶”ê°€ë©ë‹ˆë‹¤.</small>
          </div>
          <div className={styles.checkboxWrapper}>
            <input
                type="checkbox"
                id="isPublished"
                checked={isPublished}
                onChange={(e) => setIsPublished(e.target.checked)}
                className={styles.checkbox}
            />
            <label htmlFor="isPublished" className={styles.checkboxLabel}>
              {postId ? "ìˆ˜ì • í›„ ë°”ë¡œ ê²Œì‹œí•˜ê¸°" : "ë°”ë¡œ ê²Œì‹œí•˜ê¸°"}
            </label>
          </div>

          <button
              type="submit"
              disabled={isLoading || isThumbnailUploading}
              className={styles.submitButton}
          >
            {isLoading ? "ì €ì¥ ì¤‘..." : postId ? "ìˆ˜ì • ì™„ë£Œ" : "ì‘ì„± ì™„ë£Œí•˜ê³  ê²Œì‹œí•˜ê¸°"}
          </button>
        </form>
      </div>
  );
};

export default WritePage;